.column {
    text-align: center;
}

.login-menu {
    border-radius: 20px;
    background-color: white;
    min-height: 550px;
    min-width: 350px;
}

@media (max-width: 479px) {
    .login-menu {
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        right: 0;
        position: absolute;
        border-radius: 0 !important;
    }

    .login-header {
        border-radius: 0 !important;
    }

    .login-body {
        margin-top: 5rem !important;
    }
}

.login-header {
    padding: 1rem;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif; 
    flex-grow: 1;
    border-radius: 20px 20px 0px 0px;
    background-color: #00c774;
    display: flex;
    flex-direction: column;
    align-items: center;
}

h1 {
    margin-top: 0.67em;
    margin-bottom: 0;
}

.login-body {
    flex-grow: 10;
    border-radius: 0px 0px 12px 12px;
    background-color: white;
    margin-top: 2em;
}